* {
    box-sizing: border-box
}

ul,li{list-style-type:none}
.lottery-box{overflow:hidden}
.lottery-box .title{text-align:center;padding:50px 0;font-size:18px;color:#fff}
.lottery{animation:changeBg .5s ease infinite;overflow:hidden;padding:20px;width:300px;margin:0 auto;background-repeat:no-repeat;background-size:100% 100%}
.lottery .lottery-item{height: 260px;position:relative;margin-top:10px;margin-left:10px}
.lottery .lottery-item p{margin: 0}
.lottery .lottery-item ul{margin: 0;padding: 0}
.lottery .lottery-item ul li{width:33.33333333%;position:absolute;padding-right:10px}
.lottery .lottery-item ul li:nth-child(2){left:33.33333333%}
.lottery .lottery-item ul li:nth-child(3){left:66.66666666%}
.lottery .lottery-item ul li:nth-child(4){left:66.66666666%;top:84px}
.lottery .lottery-item ul li:nth-child(5){left:66.66666666%;top:168px}
.lottery .lottery-item ul li:nth-child(6){left:33.33333333%;top:168px}
.lottery .lottery-item ul li:nth-child(7){left:0;top:168px}
.lottery .lottery-item ul li:nth-child(8){left:0;top:84px}
.lottery .lottery-item ul li .box{height:74px;position:relative;text-align:center;overflow:hidden;background:url(../../assect/bg2.png) no-repeat center;background-size:100% 100%}
.lottery .lottery-item ul li .box img{display:block;height:36px;margin:0 auto;margin-top:6px;margin-bottom:5px}
.lottery .lottery-item ul li .box p{color:#708abf;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:14px}
.lottery .lottery-item ul li.on .box{background:url(../../assect/bg1.png) no-repeat center;background-size:100% 100%}
.lottery .lottery-item ul li.on .box p{color:#fff}
.lottery .lottery-item .lottery-start{position:absolute;left:33.33333333%;width:33.33333333%;top:84px;padding-right:10px}
.lottery .lottery-item .lottery-start .box{height:74px;font-size:14px;color:#fff;cursor:pointer;text-align:center;overflow:hidden;background:url(../../assect/bg1.png) no-repeat center;background-size:100% 100%}
.lottery .lottery-item .lottery-start .box p{display: flex; height: 100%;align-items: center; justify-content: center}
.lottery .lottery-item .lottery-start .box p b{margin-top: -6px;font-size:30px}
.lottery .lottery-item .lottery-start .box:active{opacity:.7}
.lottery .lottery-item .lottery-start .box.gray{background:url(../../assect/bg3.png) no-repeat center;background-size:100% 100%}
.lottery .lottery-item .lottery-start .box.gray p{color:#708abf;font-weight:700}.mask{width:100%;height:100%;background:rgba(0,0,0,.7);position:fixed;overflow:hidden;z-index:222;top:0;left:0}

@keyframes changeBg{
    0%{background-image:url(../../assect/k1.png)}
    100%{background-image:url(../../assect/k2.png)}
}