.lotteryWrapper {
    height: 100vh;
    background-image: url('../../assect/endBackground.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-attachment: fixed;
    overflow: hidden;
    .lottery{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -30%);
    }
    .thank_join {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 38px 50px;
        border-radius: 6px;
        color: #fff;
        font-size: 17px;
        background-color: rgba(0, 0, 0, .6);
    }
    .marsk {
        z-index: 999;
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(0, 0, 0, .4);
        .content {
            width: 100%;
            .price {
                width: 305px;
                height: 260px;
                margin: 0 auto;
                background: url('../../assect/redbag.png');
                background-size: 100% 100%;
                text-align: center;
                .tips {
                    margin: 0;
                    padding-top: 40px;
                    color: #A96F41;
                    font-size: 15px;
                }
                .moneyWrapper {
                    margin: 0;
                    margin-top: 18px;
                    color: #F73C37;
                    font-weight: bold;
                    .tips {
                        font-size: 12px;
                    }
                    .money {
                        font-size: 60px;
                    }
                }
                .desc {
                    margin: 0;
                    margin-top: 50px;
                    font-size: 12px;
                    color: #FFD899;
                    span {
                        line-height: 1.5;
                    }
                }
            }
        }
        .btn {
            width: 160px;
            height: 40px;
            line-height: 40px;
            background: rgba(238, 47, 42, 1);
            border-radius: 20px;
            color: #FFD899;
            font-size: 16px;
            text-align: center;
        }
    }
}